import axios from 'axios';

/**
 * Child of paternLibrary
 */
export default () => ({
    get activeTool() {
        return this._activeTool;
    },

    tools: {},
    toolbar: [],

    init() {
        this._activeTool = null;
    },

    onPatternLoaded(event) {
        // Clear out current tools (but not toolbar)
        this.tools = {};

        if (this.toolbar.length)
            return this.loadTool(this.toolbar[0].id);

        // Load tools
        // TODO: Cache this (and any tool requests made for the pattern)!
        axios.get(`/braid/patterntools`)
            .then((response) => {
                this.toolbar = response.data;

                // Preload first tool
                this.loadTool(this.toolbar[0].id);
            });
    },

    onPatternUnLoaded(event) {
        // Clear out current tools
        this._tools = {};

        // Wipe tool container
        this.$refs.toolCanvas.innerHTML = '';

        // Clear active tab
        this._activeTool = null;
    },

    loadTool(toolId) {
        const tool = this.getTool(toolId);

        if (!tool)
            console.error(`Tool id ${toolId} is incorrect.`);

        this._activeTool = tool;

        if (this.tools[tool.id])
            // Tool already fetched in this load
            return this.renderTool(tool);

        const url = `/braid/patterntools/tool/${
            encodeURIComponent(tool.className)
        }/${
            this.loadedPattern.patternId ?? this.loadedPattern.id
        }${
            (this.loadedPattern.contextId ? `/${this.loadedPattern.contextId}` : '')
        }`;

        axios.get(url)
            .then((response) => {
                this.tools[tool.id] = response.data;
                this.renderTool(tool);
            });
    },

    getTool(toolId) {
        return this.toolbar.find((tool) => {
            return tool.id === toolId;
        }) ?? null;
    },

    renderTool(tool) {
        if (!this._activeTool || tool.id !== this._activeTool.id)
            return;

        this.$refs.toolCanvas.innerHTML = this.tools[tool.id];
    },

    isActiveTool(toolId) {
        return (this._activeTool && this._activeTool.id === toolId);
    }
});